<template>
  <div class="carousel">
    <el-carousel height="400px">
      <el-carousel-item v-for="item in list" :key="item">
        <img class="slide-img" :src="item.img" />
        <div class="carousel-caption">
          <h3>{{ item.title }}</h3>
          <p>{{ item.content }}</p>
        </div>
      </el-carousel-item>
    </el-carousel>
    <span class="demostration">{{ title }}</span>
  </div>
</template>

<script>
// eslint-disable-next-line to ignore the next line.
/* eslint-disable */
const list = [
  {
    id: '1',
    title: '测试1',
    img:
      'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1578989791693&di=652b0f7a5651aade760ca112b8cc93bd&imgtype=0&src=http%3A%2F%2Ft8.baidu.com%2Fit%2Fu%3D1484500186%2C1503043093%26fm%3D79%26app%3D86%26f%3DJPEG%3Fw%3D1280%26h%3D853',
    content: '图片测试1',
    create_at: '2019-05-01'
  },
  {
    id: '2',
    title: '测试2',
    img:
      'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1578989791693&di=cee8d554a30797a1cf5b9ff6f71b51fe&imgtype=0&src=http%3A%2F%2Ft8.baidu.com%2Fit%2Fu%3D2247852322%2C986532796%26fm%3D79%26app%3D86%26f%3DJPEG%3Fw%3D1280%26h%3D853',
    content: '图片测试2',
    create_at: '2019-05-04'
  },
  {
    id: '3',
    title: '测试3',
    img:
      'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1578989791693&di=652b0f7a5651aade760ca112b8cc93bd&imgtype=0&src=http%3A%2F%2Ft8.baidu.com%2Fit%2Fu%3D1484500186%2C1503043093%26fm%3D79%26app%3D86%26f%3DJPEG%3Fw%3D1280%26h%3D853',
    content: '图片测试3',
    create_at: '2019-05-01'
  }
]

export default {
  name: 'Carousel',

  components: {},

  data() {
    return {
      list: [],
      title: 'Banner'
    }
  },

  methods: {
    updateList() {
      setTimeout(() => {
        this.list.push.apply(this.list, list)
      })
    }
  },

  mounted() {
    this.updateList()
  }
}
</script>

<style>
.carousel {
  width: 100%;
  height: 100%;
}
</style>



