<template>
  <div id="app">
    <el-container>
      <el-header><TopMenu></TopMenu></el-header>
      <el-container>
        <el-aside width="80px"><LeftMenu></LeftMenu></el-aside>
        <el-main><carousel></carousel></el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
// eslint-disable-next-line to ignore the next line.
/* eslint-disable */
import Carousel from '@/components/Carousel'
import LeftMenu from '@/components/LeftMenu'
import TopMenu from '@/components/TopMenu'

export default {
  components: {
    Carousel,
    TopMenu,
    LeftMenu
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px;
  background: #112233;
  padding: 0px;
  border: 1px;
  solid: #333;
}
</style>



