<template>
  <div class="topmenu">
    <el-menu
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleClick"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#fff"
    >
      <template v-for="item in list">
        <template v-if="item.child.length > 0">
          <el-submenu index="item.index" :key="item">
            <template slot="title">{{ item.name }}</template>
            <el-menu-item
              v-for="child in item.child"
              :key="child"
              index="child.index"
              >{{ child.name }}</el-menu-item
            >
          </el-submenu>
        </template>

        <template v-else>
          <el-menu-item index="item.index" :key="item">{{
            item.name
          }}</el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
// eslint-disable-next-line to ignore the next line.
/* eslint-disable */
import ElementUI from 'element-ui'

const list = [
  {
    index: '1',
    name: '首页',
    child: [
      {
        index: '11',
        name: '科技工作者日'
      },
      {
        index: '12',
        name: '科协系列活动'
      },
      {
        index: '13',
        name: '领导面对面'
      },
      {
        index: '14',
        name: '科技大讲堂'
      }
    ]
  },
  {
    index: '2',
    name: '品牌活动',
    child: []
  },
  {
    index: '3',
    name: '知识科普',
    child: []
  },
  {
    index: '4',
    name: '人才智库',
    child: []
  },
  {
    index: '5',
    name: '院士工作站',
    child: []
  },
  {
    index: '6',
    name: '关于科协',
    child: []
  },
  {
    index: '7',
    name: '外部资源',
    child: []
  }
]

export default {
  name: 'TopMenu',
  components: {
    ElementUI
  },

  data() {
    return {
      list: [],
      msg: 'Top Menu'
    }
  },

  methods: {
    updateList() {
      this.list.push.apply(this.list, list)
    },

    handleClick(key, keyPath) {
      console.log(key, keyPath)
    }
  },

  mounted() {
    this.updateList()
  }
}
</script>

<style scoped>
.topmenu {
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
  margin: 0 auto;
}
</style>



